body {
  color: #fff;
  background-color: #000;
}

canvas {
  height: 0;
}

.gray {
  background-color: #808080c2;
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}

.blue {
  background-color: #00f;
}

.yellow {
  background-color: #ff0;
}

.container {
  width: var(--size);
  height: var(--size);
  perspective: 500px;
  border: 1px solid #8b4513;
  margin: 100px;
}

.inner-container {
  transform: var(--transform);
  transform-style: preserve-3d;
}

.cube {
  transform: var(--transform);
  width: var(--size);
  height: var(--size);
  transform-style: preserve-3d;
  position: absolute;
}

.face {
  backface-visibility: hidden;
  width: var(--size);
  height: var(--size);
  color: #000;
  opacity: 1.5;
  border-radius: calc(var(--size) * .02);
  border: 0 solid #000;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  font-size: 3.5em;
  display: flex;
  position: absolute;
}

.front {
  transform: translateZ(calc(var(--size) / 2));
  filter: brightness();
}

.back {
  transform: translateZ(calc(-1 * var(--size) / 2)) rotateY(180deg);
  filter: brightness(10%);
}

.left {
  transform: translateX(calc(-1 * var(--size) / 2)) rotateY(-90deg);
  filter: brightness(10%);
}

.right {
  transform: translateX(calc(var(--size) / 2)) rotateY(90deg);
  filter: brightness(20%);
}

.top {
  transform: translateY(calc(-1 * var(--size) / 2)) rotateX(90deg);
  filter: brightness(50%);
}

.bottom {
  transform: translateY(calc(var(--size) / 2)) rotateX(-90deg);
}

.field {
  grid-template-rows: repeat(5, 10vw);
  grid-template-columns: repeat(5, 10vw);
  display: grid;
}

.box {
  background-color: #000;
  border: 1px solid #00008b;
  border-radius: 1vw;
  width: 10vw;
  height: 10vw;
}

.filled {
  background-color: #add8e6;
}

